.about{
    padding: 60px 20px 60px 30px;  
    background-color: #f0e47c;
    overflow: hidden;
}
.about-heading , h2{
    color: black; 
    font-family:  'Montserrat, sans-serif';
    text-align: center;
    margin: auto;
    font-weight: bold;
    font-size: 30px !important;
    
}
.about-text{
    font-size: 26px;
    line-height: 40px;
    font-family: 'Montserrat, sans-serif';
}

.about-me p{
    padding-right: 20px;
}
.about-skill ul li{
    
    display: block;
    padding-right: 40px;
    float: left;
    text-decoration: none;
    font-size: 28px;
    font-family: 'Montserrat, sans-serif';
}
.about-skill h2{
    clear: both;
    font-family: 'Montserrat, sans-serif';
    padding-bottom: 20px;
    font-weight: bold;
    text-align: center;
}
.about-skill{
    
    padding-bottom: 20px;
}

